import React, { useEffect, useState } from 'react';
import Layout from 'components/global/layout';
import './styles.scss';
// import {manuager} from '../../rxServices/rxPost';

import { collectionData } from 'rxfire/firestore';
import { tap } from 'rxjs/operators';
import {loadDB } from '../../firebase/firebaseRx';

export const Home = () => {

  const [listString, setList] = useState([]);
  useEffect(()=>{
     
    manuager();
  },[]);

  const manuager = async()=>{
    const db = await loadDB();  
    let _strGet=db.firestore().collection('books');
  
    let vaule= collectionData(_strGet, 'id')
    .pipe(
      tap(cities => console.log('This is just an observable!'))
    ).subscribe({
          next: val=>{
            setList(val);
          },
          error:err=>console.log("Lá»—i: "+err),
          complete:()=>console.log("complete"),
        });
  }
  

  
  return (
    <Layout title="Dashbord" description="This is the home page">
    <p className="home-page">This is is the home page</p>
    {listString.map((t,i)=>{
      return (
        <div key={i}>
            <div>{t.name}</div>
            <div>{t.reader}</div>
        </div>
      )
    })}
  </Layout>
  );
}
  
;

export default Home;
